  }

  .burger__close {
    display: block;
  }

  .burger__open, .burger.is-open .burger__close {
    display: none;
  }

  .burger.is-open .burger__open {
    display: block;
  }

  .burger__close-tab {
    display: none;
  }

  .burger__modal {
    width: 100%;
